const AnswersListItem = ({answer = {}}) => {
    let {name = "", text = ""} = answer;
    return (
        <div className="answer">
            <div className="answer__top" >
                <h6 className="answer__title">{name}</h6>
                <span className="answer__arrow">
                </span>
            </div>
            <div className="answer__body">
                <p className="answer__text">
                    {text}
                </p>
            </div>
        </div>
    )
};
